.loader {
  &__backdrop {
    opacity: 1;
    background-color: rgba(0, 0, 0, 0.5);

    position: fixed;
    top: 0px;
    left: 0px;

    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100%;

    transition: opacity 1s ease-in forwards;
  }
  
  &__spinner {

    width: 48px;
    height: 48px;
    border-radius: 50%;
    border-top: 3px solid hsl(0, 0%, 100%);
    border-right: 3px solid transparent;
    box-sizing: border-box;
    animation: rotation 1s linear infinite;
  }
  
  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}

.is_hidden {
  display: none;
  opacity: 0;
} // js zmienia pojawianie się tej klasy w backdrop
